@import url(https://fonts.googleapis.com/css?family=Raleway);

body {
  padding: 0 5rem;
  font-family: 'Raleway', sans-serif;
  background: #3f4751; }

a {
  color: #00ffad;
  text-decoration: none; }

h2 {
  font-size: 20px; }

nav {
  min-height: 30px;
  display: flex;
  justify-content: flex-end; }

.btn {
  outline: none;
  background: none;
  border: none;
  padding: 5px;
  margin-right: 5px;
  color: white;
  border-radius: 4px;
  font-weight: bold; }
  .btn:hover {
    cursor: pointer; }

.btn--close {
  background: #d44545; }

.icon-link {
  color: white;
  font-size: 10px;
  margin: 0 2px 0 8px; }

.icon:hover {
  cursor: pointer; }

.icon--remove {
  color: white; }

.icon--remove:hover {
  color: #d44545; }

.icon--add {
  color: #ff0; }

.icon--save {
  color: #00ffad; }

.icon--star {
  color: orange; }

.icon--cog {
  margin-right: 10px; }

.subreddit-wrap {
  display: flex;
  flex-wrap: wrap; }
  .subreddit-wrap .subreddit {
    flex-basis: 33%; }
  .subreddit-wrap .subreddit-title {
    text-decoration: none;
    color: white; }
    .subreddit-wrap .subreddit-title:hover {
      color: #e5e5e5; }
    .subreddit-wrap .subreddit-title h2 button {
      display: none; }
  .subreddit-wrap .subreddit:hover button {
    display: inline-block; }
  .subreddit-wrap .subreddit-topics {
    padding: 0 2rem 0 0; }
    .subreddit-wrap .subreddit-topics .subreddit-topic {
      list-style: none;
      margin-bottom: 0.5rem; }
    .subreddit-wrap .subreddit-topics small {
      color: #4dd1bf; }
    .subreddit-wrap .subreddit-topics .subreddit-topic__link {
      color: #e5e5e5;
      text-decoration: none; }
      .subreddit-wrap .subreddit-topics .subreddit-topic__link.stickied {
        color: #bada38;
        font-weight: bold; }
        .subreddit-wrap .subreddit-topics .subreddit-topic__link.stickied:hover {
          color: #ceef4a; }
    .subreddit-wrap .subreddit-topics .subreddit-topic__link:hover {
      color: #fff;
      font-weight: bold; }

.new-sub {
  position: fixed;
  text-align: right;
  padding: 1rem 2rem;
  color: white;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  background: -webkit-linear-gradient(139deg, #4c74a7 0%, #57c698 100%);
  background: linear-gradient(311deg, #4c74a7 0%, #57c698 100%);
  font-weight: bold; }
  .new-sub .new-sub__wrap {
    display: flex;
    align-items: center;
    margin-bottom: 10px; }
    .new-sub .new-sub__wrap img {
      height: 20px; }
  .new-sub .new-sub__input {
    margin-right: 10px;
    border: none;
    border-radius: 4px;
    padding: 4px;
    color: #4d4d4d; }

.settings-btn {
  background: #5d6978;
  font-weight: bold;
  color: white;
  padding: 5px 34px;
  outline: none;
  border: none;
  border-radius: 4px; }
  .settings-btn:hover {
    cursor: pointer; }

.start {
  display: flex;
  height: 100vh; }

.start__content {
  background: -webkit-linear-gradient(139deg, #4c74a7 0%, #57c698 100%);
  background: linear-gradient(311deg, #4c74a7 0%, #57c698 100%);
  border-radius: 5px;
  width: 300px;
  margin: auto;
  color: white;
  text-align: center;
  padding: 2rem 7rem; }
  .start__content .start__newsub {
    margin-bottom: 10px; }

.new-sub__input.new-sub__input--start {
  width: 88%;
  font-size: 24px;
  border-radius: 5px;
  border: 1px solid #d2d2d2;
  padding: 0.5rem; }

.button-wrap {
  text-align: right; }

